import React, { Component } from 'react';
import { connect } from 'react-redux';
import { updateShare } from '../../actions/modalController';
import { updateFinalizePage, updateFinalizeData } from '../../actions/finalizeController';
import { updateButtons } from '../../actions/buttonController';
import withStyles from '@material-ui/core/styles/withStyles';
import { CSSTransition, TransitionGroup } from 'react-transition-group';
import classNames from "classnames";
import Card from "../Card/Card.jsx";
import StepHeader from './StepHeader.jsx';
import FinalizePageOne from './FinalizePageOne.jsx';
import FinalizePageTwo from './FinalizePageTwo.jsx';
import FinalizePageThree from './FinalizePageThree.jsx';

function mapStateToProps(state) {
  return {
    share: state.modalReducer.share,
    step: state.finalizeReducer.step,
    steps: state.finalizeReducer.steps,
    fullName: state.finalizeReducer.fullName,
    email: state.finalizeReducer.email,
    phone: state.finalizeReducer.phone,
    animation: state.finalizeReducer.animation
  };
}

function mapDispatchToProps(dispatch) {
  return {
    updateShare: share => dispatch(updateShare(share)),
    updateFinalizePage: (step, steps) => dispatch(updateFinalizePage(step, steps)),
    updateFinalizeData: (data) => dispatch(updateFinalizeData(data)),
    updateButtons: data => dispatch(updateButtons(data))
  };
}

class ShareModal extends Component {
  constructor (props) {
    super(props);
    this.state = {
      animation: false
    }
    this.props.updateButtons({ menu: false });
  }
  componentDidMount() {
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
    var steps = [{ stepName: 'Your Information', header: 'Schedule With Our Design Consultant', active: true, completed: false, bodyContent: <FinalizePageOne /> },
      { stepName: 'Save & Send', header: 'Schedule With Our Design Consultant', active: false, completed: false, bodyContent: <FinalizePageTwo /> },
      { stepName: 'Share', header: 'Schedule With Our Design Consultant', active: false, completed: false, bodyContent: <FinalizePageThree /> }]
    this.props.updateFinalizePage(0, steps)
  }
  render () {
    var content
    const { classes } = this.props;
    if (this.props.steps.length !== 0) {
      content = this.props.steps[this.props.step].bodyContent
    } else {
      content = 'please provide an array'
    }
    return (
      <Card className={classNames(classes.main, 'fadeIn')}>
        <StepHeader step={this.props.step} steps={this.props.steps} location='final' />
        <TransitionGroup>
          <CSSTransition
            in={this.props.animation}
            timeout={2000}
            classNames={{ enter: 'customSlideInLeft', exit: 'customSlideOutRight' }}
            onExited={() => {
              this.props.updateFinalizeData({
                animation: false
              })
            }}
          >
            <div>
              {content}
            </div>
          </CSSTransition>
        </TransitionGroup>
      </Card>
    );
  }
}

const style = {
  main: {
    zIndex: 40,
    textAlign: 'center',
    width: '66vw',
    height: '74vh',
    marginLeft: '17vw',
    marginTop: '16vh',
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(style)(ShareModal));
