const initialState = {
  share: false,
  save: false,
  spec: false
}

export default function(state = initialState, action) {
  switch (action.type) {
    case "updateShare":
      return {
        ...state,
        share: action.payload
      };
    case "updateSave":
      return {
        ...state,
        save: action.payload
      };
    case "updateSpec":
      return {
        ...state,
        spec: action.payload
      };
    default:
      return state;
  }
}
