import React, { Component } from 'react';
import classNames from "classnames";
import { connect } from 'react-redux';
import { updateSave } from '../../actions/modalController';
import { updateButtons } from '../../actions/buttonController';
import { handleNavigation } from '../../actions/navigationController';
import withStyles from '@material-ui/core/styles/withStyles';
import Card from "../Card/Card.jsx";
import CardHeader from "../Card/CardHeader.jsx";
import CardBody from "../Card/CardBody.jsx";
import TextField from '@material-ui/core/TextField';
import Typography from '@material-ui/core/Typography';
import GridContainer from "../Grid/GridContainer.jsx";
import GridItem from "../Grid/GridItem.jsx";
import ButtonMaterial from '@material-ui/core/Button';
import BG from '../../assets/img/finalResult.jpg'

function mapStateToProps(state) {
  return {
    save: state.modalReducer.save
  };
}

function mapDispatchToProps(dispatch) {
  return {
    updateSave: save => dispatch(updateSave(save)),
    updateButtons: data => dispatch(updateButtons(data)),
    handleNavigation: route => dispatch(handleNavigation(route))
  };
}

class SaveModal extends Component {
  constructor (props) {
    super(props);
    this.state = {
      animation: false,
      name: ''
    }
    this.props.updateButtons({ menu: false });
    this.updateName = this.updateName.bind(this);
  }
  componentDidMount() {
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
  }
  updateName(event) {
    this.setState({
      name: event.target.value
    })
  }
  render () {
    const { classes } = this.props;

    return (
      <Card className={classNames(classes.root, 'fadeIn')}>
        <CardHeader className={classes.cardHeader}>
          <Typography variant='body1' className={classes.headerText}>
            Save Design Draft
          </Typography>
        </CardHeader>
        <CardBody>
          <GridContainer justify='center' className={classes.cardBodyContain}>
            <GridItem xs={7}>
              <img src={BG} alt='preview' className={classes.preview}/>
            </GridItem>
            <GridItem xs={6}>
              <TextField
                label="* File Name"
                id="Name"
                className={classes.designName}
                value={this.state.name}
                onChange={this.updateName}
                fullWidth={true}
                margin="dense"
              />
            </GridItem>
          </GridContainer>
          <GridContainer justify='center' className={classes.buttonsContain}>
            <GridItem xs={3}>
              <ButtonMaterial
                className={classes.button}
                variant="outlined"
                size="large"
                onClick={() => {
                  this.props.updateButtons({ menu: true })
                  this.props.handleNavigation('My Designs')
                  this.props.updateSave(false)
                }}
                >
                Save
              </ButtonMaterial>
            </GridItem>
            <GridItem xs={3}>
              <ButtonMaterial
                className={classes.buttonSecondary}
                variant="outlined"
                size="large"
                onClick={() => {
                  this.props.updateSave(false)
                  this.props.updateButtons({ menu: true })
                }}
                >
                Cancel
              </ButtonMaterial>
            </GridItem>
          </GridContainer>
        </CardBody>
      </Card>
    );
  }
}

const style = {
  root: {
    zIndex: 40,
    textAlign: 'center',
    width: '66vw',
    height: '74vh',
    marginLeft: '17vw',
    marginTop: '16vh',
  },
  cardHeader: {
    fontFamily: '"Montserrat", sans-serif',
    width: "auto",
    fontWeight: 800,
    fontSize: '1.1em',
    padding: '2vh',
    backgroundColor: '#252525',
    textAlign: "center",
    color: '#fff',
    boxShadow: '2px 4px 8px rgba(0, 0, 0, .5)',
  },
  headerText: {
    color: '#fff !important'
  },
  cardBodyContain: {
    width: '100%',
    height: '44vh',
    position: 'absolute',
    left: 0,
    top: '8vh',
    margin: 0,
    textAlign: 'center',
  },
  preview: {
    borderRadius: '15px',
    marginTop: '2vh',
    marginBottom: '2vh',
    width: 'auto',
    height: '36vh',
    boxShadow: '2px 4px 8px rgba(0, 0, 0, .5)',
  },
  buttonsContain: {
    position: 'absolute',
    bottom: '6vh',
    left: 0,
    width: '100%',
    margin: 0
  },
  button: {
    fontFamily: 'Montserrat, sans-serif !important',
    color: '#fff',
    backgroundColor: '#ff0000',
    border: 'none',
    '&:hover': {
      background: '#ff0000',
      color: '#ffffff',
    }
  },
  buttonSecondary: {
    fontFamily: 'Montserrat, sans-serif',
    background: 'none',
  },
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(style)(SaveModal));
