/* eslint-disable */
import React from "react";
import { connect } from 'react-redux';
// nodejs library that concatenates classes
import classNames from "classnames";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import Icon from "@material-ui/core/Icon";
import Grid from '@material-ui/core/Grid';
import Tooltip from "@material-ui/core/Tooltip";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
// @material-ui/icons
import Visibility from "@material-ui/icons/Visibility";
import VisibilityOff from "@material-ui/icons/VisibilityOff";
import Settings from "@material-ui/icons/Settings";
import Face from "@material-ui/icons/Face";
import CreditCard from "@material-ui/icons/CreditCard";
import Add from "@material-ui/icons/Add";
import Favorite from "@material-ui/icons/Favorite";
import Info from "@material-ui/icons/InfoOutlined";
// core components
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import ButtonMaterial from '@material-ui/core/Button';
import GridContainer from "./components/Grid/GridContainer.jsx";
import GridItem from "./components/Grid/GridItem.jsx";
import HeaderLinks from "./components/Header/HeaderLinks.jsx";
import NavPills from "./components/NavPills/NavPills.jsx";
import Card from "./components/Card/Card.jsx";
import CardBody from "./components/Card/CardBody.jsx";
import CardHeader from "./components/Card/CardHeader.jsx";
import Badge from "./components/Badge/Badge.jsx";
import Muted from "./components/Typography/Muted.jsx";
import Parallax from "./components/Parallax/Parallax.jsx";
import Clearfix from "./components/Clearfix/Clearfix.jsx";
import Button from "./components/CustomButtons/Button.jsx";
import CustomInput from './components/CustomInput/CustomInput.jsx';
import InputAdornment from "@material-ui/core/InputAdornment";
import { updateButtons } from './actions/buttonController';

import profilePageStyle from "./assets/jss/material-kit-pro-react/views/profilePageStyle.jsx";
import BG from "./assets/img/profileBGFinal.jpg";
import logo from "./assets/img/LogoMVN.svg";
import './animate.css';

function mapStateToProps(state) {
  return {
    // for when profile redux is added
  };
}

function mapDispatchToProps(dispatch) {
  return {
    updateButtons: data => dispatch(updateButtons(data))
  };
}

class ProfilePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      accountEdit: 'view',
      email: 'example@website.com',
      first: 'John',
      last: 'Doe',
      phone: '(624) 112 - 1234',
      address1: '1111 w Pacesetter dr',
      address2: 'Unit. 296',
      state: 'AZ',
      city: 'Scottsdale',
      zip: '85224',
      showPass: false,
      pass: '',
      passNew: '',
      passNewConfirm: '',
      tooltip: false,
    }
    this.props.updateButtons({ menu: true });
    this.updateEmail = this.updateEmail.bind(this);
    this.updateFirst = this.updateFirst.bind(this);
    this.updateLast = this.updateLast.bind(this);
    this.updatePhone = this.updatePhone.bind(this);
    this.updateAddress1 = this.updateAddress1.bind(this);
    this.updateAddress2 = this.updateAddress2.bind(this);
    this.updateZip = this.updateZip.bind(this);
    this.updateCity = this.updateCity.bind(this);
    this.handleState = this.handleState.bind(this);
    this.updatePassword = this.updatePassword.bind(this);
    this.updatePasswordNew = this.updatePasswordNew.bind(this);
    this.updatePasswordNewConfirm = this.updatePasswordNewConfirm.bind(this);
  }
  componentDidMount() {
    window.scrollTo(0, 0);
    document.body.scrollTop = 0;
  }
  updateEmail(event) {
    this.setState({
      email: event.target.value
    });
  }
  updateFirst(event) {
    this.setState({
      first: event.target.value
    });
  }
  updateLast(event) {
    this.setState({
      last: event.target.value
    });
  }
  updatePhone(event) {
    this.setState({
      phone: event.target.value
    });
  }
  updateAddress1(event) {
    this.setState({
      address1: event.target.value
    });
  }
  updateAddress2(event) {
    this.setState({
      address2: event.target.value
    });
  }
  updateZip(event) {
    this.setState({
      zip: event.target.value
    });
  }
  updateCity(event) {
    this.setState({
      city: event.target.value
    });
  }
  handleState(event) {
    this.setState({
      state: event.target.value
    });
  }
  updatePassword(event) {
    this.setState({
      pass: event.target.value
    });
  }
  updatePasswordNew(event) {
    this.setState({
      passNew: event.target.value
    });
  }
  updatePasswordNewConfirm(event) {
    this.setState({
      passNewConfirm: event.target.value
    });
  }
  render() {
    var Account,
      pass,
      passNew,
      passNewConfirm,
      icon,
      tooltipModal
    const { classes, ...rest } = this.props;
    const AccountEdit = (
      <Card className={classNames(classes.editCard, 'fadeIn')}>
        <CardHeader className={classes.cardHeader}>
          <h2 className={classes.cardTitle}>My Profile</h2>
        </CardHeader>
        <form>
          <GridContainer justify='center' className={classes.formContainer}>
            <GridItem xs={10} className={classes.gridItem}>
              <TextField
                id="standard-dense"
                label="* Email"
                className={classes.input}
                value={this.state.email}
                onChange={this.updateEmail}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
            <GridItem xs={5} className={classes.gridItem}>
              <TextField
                id="standard-dense"
                label="* First Name"
                className={classes.input}
                value={this.state.first}
                onChange={this.updateFirst}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
            <GridItem xs={5} className={classes.gridItem}>
              <TextField
                label="* Last Name"
                id="last"
                className={classes.input}
                value={this.state.last}
                onChange={this.updateLast}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
            <GridItem xs={10} className={classes.gridItem}>
              <TextField
                label="* Address line 1"
                id="address1"
                className={classes.input}
                value={this.state.address1}
                onChange={this.updateAddress1}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
            <GridItem xs={10} className={classes.gridItem}>
              <TextField
                label="* Address line 2"
                id="address2"
                className={classes.input}
                value={this.state.address2}
                onChange={this.updateAddress2}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
            <GridItem xs={5} className={classes.gridItem}>
              <FormControl
                fullWidth
                className={classes.selectFormControl}>
                <InputLabel
                  htmlFor="state"
                  className={classes.selectLabel}>
                  * State
                </InputLabel>
                <Select
                  MenuProps={{
                    className: classes.selectMenu
                  }}
                  classes={{
                    select: classes.select
                  }}
                  value={this.state.state}
                  onChange={this.handleState}
                  inputProps={{
                    name: "state",
                    id: "state"
                  }}>
                  <MenuItem
                    classes={{
                      root: classes.selectMenuItem,
                      selected: classes.selectMenuItemSelected
                    }}
                    value="AZ">
                    Arizona
                  </MenuItem>
                  <MenuItem
                    classes={{
                      root: classes.selectMenuItem,
                      selected: classes.selectMenuItemSelected
                    }}
                    value="CA">
                    California
                  </MenuItem>
                  <MenuItem
                    classes={{
                      root: classes.selectMenuItem,
                      selected: classes.selectMenuItemSelected
                    }}
                    value="TX">
                    Texas
                  </MenuItem>
                </Select>
              </FormControl>
            </GridItem>
            <GridItem xs={5} className={classes.gridItem}>
              <TextField
                label="* City"
                id="city"
                className={classes.input}
                value={this.state.city}
                onChange={this.updateCity}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
            <GridItem xs={5} className={classes.gridItem}>
              <TextField
                label="* Postal Code"
                id="post"
                className={classes.input}
                value={this.state.zip}
                onChange={this.updateZip}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
            <GridItem xs={5}></GridItem>
              <GridItem xs={5}>
                <ButtonMaterial className={classes.buttonSaveEdit} size="large" onClick={() => this.setState({accountEdit: 'view'})}>
                  Save
                </ButtonMaterial>
              </GridItem>
            <GridItem xs={5}>
              <ButtonMaterial className={classes.buttonCancelEdit} variant="outlined" size="large" onClick={() => this.setState({accountEdit: 'view'})}>
                Cancel
              </ButtonMaterial>
            </GridItem>
          </GridContainer>
        </form>
      </Card>
    );
    const AccountView = (
      <Card className={classNames(classes.viewCard, 'fadeInTwo')}>
        <CardHeader className={classes.cardHeader}>
          <h2 className={classes.cardTitle}>My Profile</h2>
        </CardHeader>
        <GridContainer justify='center' className={classes.formContainer}>
          <GridItem xs={10} className={classes.gridItem}>
            <TextField
              disabled
              id="standard-dense"
              label="* Email"
              className={classes.inputView}
              value={this.state.email}
              fullWidth={true}
              margin="dense"
              type='text'
            />
          </GridItem>
          <GridItem xs={5} className={classes.gridItem}>
            <TextField
              id="standard-dense"
              label="* First Name"
              className={classes.inputView}
              value={this.state.first}
              disabled
              fullWidth={true}
              margin="dense"
              type='text'
            />
          </GridItem>
          <GridItem xs={5} className={classes.gridItem}>
            <TextField
              label="* Last Name"
              id="last"
              className={classes.inputView}
              value={this.state.last}
              disabled
              fullWidth={true}
              margin="dense"
              type='text'
            />
          </GridItem>
          <GridItem xs={10} className={classes.gridItem}>
            <TextField
              label="* Address line 1"
              id="address1"
              className={classes.inputView}
              value={this.state.address1}
              disabled
              fullWidth={true}
              margin="dense"
              type='text'
            />
          </GridItem>
          <GridItem xs={10} className={classes.gridItem}>
            <TextField
              label="* Address line 2"
              id="address2"
              className={classes.inputView}
              value={this.state.address2}
              disabled
              fullWidth={true}
              margin="dense"
              type='text'
            />
          </GridItem>
          <GridItem xs={5} className={classes.gridItem}>
            <FormControl
              fullWidth
              className={classes.selectFormControlView}>
              <InputLabel
                htmlFor="state"
                className={classes.selectLabel}>
                * State
              </InputLabel>
              <Select
                disabled
                MenuProps={{
                  className: classes.selectMenu
                }}
                classes={{
                  select: classes.select
                }}
                value={this.state.state}
                onChange={this.handleState}
                inputProps={{
                  name: "state",
                  id: "state"
                }}>
                <MenuItem
                  classes={{
                    root: classes.selectMenuItem,
                    selected: classes.selectMenuItemSelected
                  }}
                  value="AZ">
                  Arizona
                </MenuItem>
                <MenuItem
                  classes={{
                    root: classes.selectMenuItem,
                    selected: classes.selectMenuItemSelected
                  }}
                  value="CA">
                  California
                </MenuItem>
                <MenuItem
                  classes={{
                    root: classes.selectMenuItem,
                    selected: classes.selectMenuItemSelected
                  }}
                  value="TX">
                  Texas
                </MenuItem>
              </Select>
            </FormControl>
          </GridItem>
          <GridItem xs={5} className={classes.gridItem}>
            <TextField
              label="* City"
              id="city"
              className={classes.inputView}
              value={this.state.city}
              disabled
              fullWidth={true}
              margin="dense"
              type='text'
            />
          </GridItem>
          <GridItem xs={5} className={classes.gridItem}>
            <TextField
              label="* Postal Code"
              id="post"
              className={classes.inputView}
              value={this.state.zip}
              disabled
              fullWidth={true}
              margin="dense"
              type='text'
            />
          </GridItem>
          <GridItem xs={5}></GridItem>
          <GridItem xs={5}>
            <ButtonMaterial className={classes.buttonEdit} variant="outlined" size="large" onClick={() => this.setState({accountEdit: 'edit'})}>
              Edit Profile
            </ButtonMaterial>
          </GridItem>
          <GridItem xs={5}>
            <ButtonMaterial className={classes.buttonReset} variant="outlined" size="large" onClick={() => this.setState({accountEdit: 'reset'})}>
              Reset Password
            </ButtonMaterial>
          </GridItem>
        </GridContainer>
      </Card>
    );
    if (this.state.showPass) {
      pass = (
        <GridItem xs={10}>
          <TextField
            label="* Old Password"
            id="password"
            className={classes.input}
            value={this.state.pass}
            onChange={this.updatePassword}
            fullWidth={true}
            margin="dense"
            type='text'
          />
        </GridItem>
      );
      passNew = (
        <GridItem xs={10}>
          <TextField
            label="* New Password"
            id="passwordNew"
            className={classes.input}
            value={this.state.passNew}
            onChange={this.updatePasswordNew}
            fullWidth={true}
            margin="dense"
            type='text'
          />
        </GridItem>
      );
      passNewConfirm = (
        <GridItem xs={12}>
          <GridContainer justify='center'>
            <GridItem xs={10}>
              <TextField
                label="* New Password Confirmation"
                id="passwordCon"
                className={classes.input}
                value={this.state.passNewConfirm}
                onChange={this.updatePasswordNewConfirm}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
          </GridContainer>
        </GridItem>
      );
      icon = <VisibilityOff className={classNames(classes.inputIconsColor, classes.showIcon)} onClick={() => this.setState({ showPass: false })}/>
    } else {
      pass = (
        <GridItem xs={10}>
          <TextField
            label="* Old Password"
            id="password"
            className={classes.input}
            value={this.state.pass}
            onChange={this.updatePassword}
            fullWidth={true}
            margin="dense"
            type='password'
          />
        </GridItem>
      );
      passNew = (
        <GridItem xs={10}>
          <TextField
            label="* New Password"
            id="passwordNew"
            className={classes.input}
            value={this.state.passNew}
            onChange={this.updatePasswordNew}
            fullWidth={true}
            margin="dense"
            type='password'
          />
        </GridItem>
      );
      passNewConfirm = (
        <GridItem xs={12}>
          <GridContainer justify='center'>
            <GridItem xs={10}>
              <TextField
                label="* New Password Confirmation"
                id="passwordCon"
                className={classes.input}
                value={this.state.passNewConfirm}
                onChange={this.updatePasswordNewConfirm}
                fullWidth={true}
                margin="dense"
                type='password'
              />
            </GridItem>
          </GridContainer>
        </GridItem>
      );
      icon = <Visibility className={classNames(classes.inputIconsColor, classes.showIcon)} onClick={() => this.setState({ showPass: true })}/>
    }
    if (this.state.tooltip) {
      tooltipModal = (
        <div className={classes.tooltipModal}>
          <Typography variant='body2'>
            * Password requires 8 characters, 1 uppercase,
          </Typography>
          <Typography variant='body2'>
            1 lowercase, 1 number and no spaces
          </Typography>
        </div>
      );
    } else {
      tooltipModal = <div></div>
    }
    var PasswordView = (
      <Card className={classNames(classes.passwordCard, 'fadeIn')}>
        <CardHeader className={classes.cardHeader}>
          <h2 className={classes.cardTitle}>Reset Password</h2>
        </CardHeader>
        <div className={classes.iconContainer}>
          {icon}
          <button onClick={() => this.setState({ tooltip: !this.state.tooltip })} className={classes.infoButton}>
            <Info className={classes.infoIconColor} />
          </button>
        </div>
        <GridContainer justify='center' className={classes.passwordContainer}>
          {pass}
          {passNew}
          {passNewConfirm}
        </GridContainer>
        {tooltipModal}
        <GridContainer justify='center'>
          <GridItem xs={5}>
            <ButtonMaterial className={classes.buttonEdit} variant="outlined" size="large" onClick={() => this.setState({accountEdit: 'reset'})}>
              Reset
            </ButtonMaterial>
          </GridItem>
          <GridItem xs={5}>
            <ButtonMaterial className={classes.buttonCancel} variant="outlined" size="large" onClick={() => this.setState({accountEdit: 'view'})}>
              Cancel
            </ButtonMaterial>
          </GridItem>
        </GridContainer>
      </Card>
    );
    if (this.state.accountEdit === 'edit') {
      Account = AccountEdit
    } else if (this.state.accountEdit === 'view') {
      Account = AccountView
    } else {
      Account = PasswordView
    }
    return (
      <div className={classes.background}>
        <div
          className={classNames(classes.pageHeader, 'fadeInTwo')}
          style={{
            backgroundImage: "url(" + BG + ")",
            backgroundSize: "cover",
            backgroundPosition: "top center"
          }}
        ></div>
        <div className={classes.main}>
          {Account}
        </div>
      </div>
    );
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(profilePageStyle)(ProfilePage));
