import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import ButtonMaterial from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import withStyles from "@material-ui/core/styles/withStyles";
import { updateButtons } from './actions/buttonController';
import './home.css';
import logo from "./assets/img/LogoMVN.svg";
import video from "./assets/img/BackyardBuilderBG2.mp4";
var url

function mapStateToProps(state) {
  // wont load without both mapStateToProps & mapDispatchToProps for some strange reason
  return {}
}

function mapDispatchToProps(dispatch) {
  return {
    updateButtons: data => dispatch(updateButtons(data))
  };
}

class Home extends Component {
  constructor (props) {
    super(props);
    this.state = {
      toRegister: false
    }
    this.props.updateButtons({ menu: false });
  }

  componentDidMount() {
    var vid = document.getElementById("vid");
    vid.playbackRate = 0.7;
  }

  render () {
    const { classes } = this.props;
    if (this.state.toRegister) {
      url = <Redirect to='/register' />
    }
    return (
      <div className={classes.pageHeader}>
        {url}
        <div className={this.state.backClass}>
          <img src={logo} className={classes.logo} alt='logo' />
          <Typography variant='h4' className={classes.logoHeader}>
            Backyard Builder
          </Typography>
          <div className={classes.videoContainer}>
            <video autoPlay muted loop id={'vid'} className={classes.video}>
              <source src={video} type="video/mp4"/>
              Your browser doesn't support the video tag
            </video>
          </div>
          <ButtonMaterial className={'buttonStart'} size="large" onClick={() => this.setState({toRegister: true})}>
            Start Designing
          </ButtonMaterial>
        </div>
      </div>
    );
  }
}

const style = {
  pageHeader: {
    border: "0",
    margin: "0",
    display: "flex!important",
    top: 0,
    left: 0,
    right: 0,
    bottom: 0,
    position: "absolute",
    alignItems: "center",
    overflow: 'hidden',
    zIndex: -1,
    paddingTop: '35vh',
  },
  button: {
    margin: 0,
    width: '60%',
    color: '#ffffff',
    borderColor: '#ffffff'
  },
  video: {
    zIndex: 11,
    height: '110vh',
    position: 'absolute',
    overflow: 'hidden',
    top: '-5vh',
    left: 0,
  },
  videoContainer: {
    "&:before": {
      background: "rgba(0, 0, 0, 0.3)"
    },
    "&:before,&:after": {
      position: "absolute",
      zIndex: "12",
      width: "100%",
      height: "100%",
      display: "block",
      left: "0",
      top: "0",
      content: '""'
    }
  },
  logo: {
    zIndex: 13,
    width: '40vw',
    position: 'absolute',
    overflow: 'hidden',
    top: '10vh',
    left: '33vw',
  },
  logoHeader: {
    zIndex: 13,
    width: '40vw',
    textAlign: 'center',
    position: 'absolute',
    overflow: 'hidden',
    top: '32vh',
    left: '40vw',
    color: '#fff',
    fontFamily: 'Lato, sans-serif',
    fontWeight: 'bold',
  },
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(style)(Home));
