import React, { Component } from 'react';
import classNames from "classnames";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
import FormControl from "@material-ui/core/FormControl";
import InputLabel from "@material-ui/core/InputLabel";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import TextField from '@material-ui/core/TextField';
import Card from "../Card/Card.jsx";
import StepHeader from './StepHeader.jsx'
// @material-ui/icons
import GridContainer from "../Grid/GridContainer.jsx";
import GridItem from "../Grid/GridItem.jsx";
import ButtonMaterial from '@material-ui/core/Button';
import { connect } from 'react-redux';
import { updateRegisterPage, updateRegisterData } from '../../actions/registerController';
import { handleNavigation } from '../../actions/navigationController';
import { startTutorial } from '../../actions/tutorialController';
import '../../animate.css';
import '../../transition.css';

function mapStateToProps(state) {
  return {
    step: state.registerReducer.step,
    steps: state.registerReducer.steps,
    first: state.registerReducer.first,
    last: state.registerReducer.last,
    address1: state.registerReducer.address1,
    address2: state.registerReducer.address2,
    zip: state.registerReducer.zip,
    city: state.registerReducer.city,
    state: state.registerReducer.state
  };
}

function mapDispatchToProps(dispatch) {
  return {
    updateRegisterPage: (step, steps) => dispatch(updateRegisterPage(step, steps)),
    updateRegisterData: data => dispatch(updateRegisterData(data)),
    handleNavigation: route => dispatch(handleNavigation(route)),
    startTutorial: () => dispatch(startTutorial())
  };
}

class RegisterPageThree extends Component {
  constructor (props) {
    super(props);
    this.updateFirst = this.updateFirst.bind(this);
    this.updateLast = this.updateLast.bind(this);
    this.updatePhone = this.updatePhone.bind(this);
    this.updateAddress1 = this.updateAddress1.bind(this);
    this.updateAddress2 = this.updateAddress2.bind(this);
    this.updateZip = this.updateZip.bind(this);
    this.updateCity = this.updateCity.bind(this);
    this.handleState = this.handleState.bind(this);
  }
  updateFirst(event) {
    this.props.updateRegisterData({
      first: event.target.value
    });
  }
  updateLast(event) {
    this.props.updateRegisterData({
      last: event.target.value
    });
  }
  updatePhone(event) {
    this.props.updateRegisterData({
      phone: event.target.value
    });
  }
  updateAddress1(event) {
    this.props.updateRegisterData({
      address1: event.target.value
    });
  }
  updateAddress2(event) {
    this.props.updateRegisterData({
      address2: event.target.value
    });
  }
  updateZip(event) {
    this.props.updateRegisterData({
      zip: event.target.value
    });
  }
  updateCity(event) {
    this.props.updateRegisterData({
      city: event.target.value
    });
  }
  handleState(event) {
    this.props.updateRegisterData({
      state: event.target.value
    });
  }
  render () {
    const { classes } = this.props;
    return (
      <Card className={classNames(classes.rootCard, 'fadeIn')}>
        <StepHeader step={this.props.step} steps={this.props.steps} location='reg' />
        <form className={classes.form}>
          <GridContainer justify='center' className={classes.formContainer}>
            <GridItem xs={5} className={classes.gridItem}>
              <TextField
                id="standard-dense"
                label="* First Name"
                className={classes.input}
                value={this.props.first}
                onChange={this.updateFirst}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
            <GridItem xs={5} className={classes.gridItem}>
              <TextField
                label="* Last Name"
                id="last"
                className={classes.input}
                value={this.props.last}
                onChange={this.updateLast}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
            <GridItem xs={10} className={classes.gridItem}>
              <TextField
                label="* Address line 1"
                id="address1"
                className={classes.input}
                value={this.props.address1}
                onChange={this.updateAddress1}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
            <GridItem xs={10} className={classes.gridItem}>
              <TextField
                label="* Address line 2"
                id="address2"
                className={classes.input}
                value={this.props.address2}
                onChange={this.updateAddress2}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
            <GridItem xs={5} className={classes.gridItem}>
              <FormControl
                fullWidth
                className={classes.selectFormControl}>
                <InputLabel
                  htmlFor="state"
                  className={classes.selectLabel}>
                  * State
                </InputLabel>
                <Select
                  MenuProps={{
                    className: classes.selectMenu
                  }}
                  classes={{
                    select: classes.select
                  }}
                  value={this.props.state}
                  onChange={this.handleState}
                  inputProps={{
                    name: "state",
                    id: "state"
                  }}>
                  <MenuItem
                    classes={{
                      root: classes.selectMenuItem,
                      selected: classes.selectMenuItemSelected
                    }}
                    value="AZ">
                    Arizona
                  </MenuItem>
                  <MenuItem
                    classes={{
                      root: classes.selectMenuItem,
                      selected: classes.selectMenuItemSelected
                    }}
                    value="CA">
                    California
                  </MenuItem>
                  <MenuItem
                    classes={{
                      root: classes.selectMenuItem,
                      selected: classes.selectMenuItemSelected
                    }}
                    value="TX">
                    Texas
                  </MenuItem>
                </Select>
              </FormControl>
            </GridItem>
            <GridItem xs={5} className={classes.gridItem}>
              <TextField
                label="* City"
                id="city"
                className={classes.input}
                value={this.props.city}
                onChange={this.updateCity}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
            <GridItem xs={5} className={classes.gridItem}>
              <TextField
                label="* Postal Code"
                id="post"
                className={classes.input}
                value={this.props.zip}
                onChange={this.updateZip}
                fullWidth={true}
                margin="dense"
                type='text'
              />
            </GridItem>
            <GridItem xs={5}></GridItem>
          </GridContainer>
          <div className={classes.textCenter}>
            <ButtonMaterial
              className={classes.buttonLogin}
              variant="outlined"
              size="large"
              onClick={() => {
                this.props.handleNavigation('New Design');
                setTimeout(500, this.props.startTutorial());
              }}
            >
              Done
            </ButtonMaterial>
          </div>
        </form>
      </Card>
    )
  }
}

const style = {
  textCenter: {
    textAlign: 'center',
  },
  formContainer: {
    height: '50vh',
    textAlign: 'center',
  },
  buttonLogin: {
    textAlign: 'center',
    fontFamily: 'Montserrat, sans-serif !important',
    color: '#fff',
    backgroundColor: '#ff0000',
    border: 'none',
    fontSize: '1em',
    marginBottom: '4vh',
    marginTop: '5vh',
    '&:hover': {
      background: '#ff0000',
      color: '#ffffff',
    }
  },
  button: {
    textAlign: 'center',
    fontFamily: 'Montserrat, sans-serif',
    textDecoration: 'underline',
    textUnderlinePosition: 'under',
    paddingLeft: '1vw',
    marginBottom: '6vh',
    width: '100%',
    background: 'none',
    border: 'none',
    '&:hover': {
      outline: 'none',
    }
  },
  underline: {
    borderBottom: '1px solid #ff0000',
  },
  gridItem: {
    marginTop: '2vh'
  },
  input: {
    color: '#252525',
    fontWeight: 400,
    lineHeight: 1.42857,
    fontSize: '14px',
    margin: '12px 0 4px'
  },
  inputIconsColor: {
    color: "#252525",
    opacity: 0.7
  },
  form: {
    margin: "0",
  },
  selectFormControl: {
    color: '#252525',
    textAling: 'left',
    fontWeight: 400,
    lineHeight: 1.42857,
    fontSize: '14px',
    margin: '12px 0 7px'
  },
}

export default connect(mapStateToProps, mapDispatchToProps)(withStyles(style)(RegisterPageThree));
