const initialState = {
  bottom: false,
  drawerView: '',
  search: false
}
export default function(state = initialState, action) {
  switch (action.type) {
    case "updateDrawer":
      return {
        ...state,
        bottom: action.payload
      };
    case "updateDrawerView":
      return {
        ...state,
        drawerView: action.payload.drawerView
      };
    case "updateSearch":
      return {
        ...state,
        search: action.payload
      };
    default:
      return state;
  }
}
